import React from "react";
import Particle from "react-tsparticles";
import particlesConfig from "../assets/particlesConfig.json";
import { loadFull } from "tsparticles";

function App() {
  const particlesInit = async (main) => {
    await loadFull(main);
  };

  return (
    <>
      <Particle
        id="tsparticles"
        init={particlesInit}
        options={particlesConfig}
      />
      <div className="App">
        <div className="App-text">
          <h1 className="App-text__title">Mohamed Elazap</h1>
          <h2 className="App-text__body">I am a front end web developer.</h2>
        </div>
      </div>
    </>
  );
}

export default App;
